import { useEffect, useState } from 'react';
import useDimensions from 'hooks/collage/dimensions';
import useOptions from 'hooks/collage/options';
import useActions from 'hooks/collage/actions';
import useImages from 'hooks/collage/images';
import useTree from 'hooks/collage/tree';

import helpers from 'components/collage/helpers';
import collageapi from 'api/collage';

export default function useCollage() {
  const constants = {
    tolerance: 0.1,
    resizeImageWidth: 1500,
    aspectRatio: 0.7,
    paperSizes: {
      A4: { width: 2480, height: 3508, text: '8 X 11 inches' },
      A3: { width: 3508, height: 4961, text: '11 X 16 inches' },
    },
  };

  const dimensions = useDimensions(constants.aspectRatio);
  const options = useOptions();
  const actions = useActions();
  const images = useImages(constants.resizeImageWidth);
  const treeState = useTree({}, options.setOptions);
  const [thumbnail, setThumbnail] = useState(null);

  const handleClear = () => {
    treeState.clear();
    images.clear();
    options.clear();
  };

  useEffect(() => {
    if (
      (images.newImgCount > 0 &&
        images.newImgCount === images.newImagesLoaded) ||
      actions.refresh
    ) {
      images.setNewImageCount(0);
      images.setNewImagesLoaded(0);
      treeState.reAdjust(
        images.images,
        constants.tolerance,
        dimensions.width,
        dimensions.height - dimensions.textHeight,
        options.options
      );
      actions.setRefresh(false);
    }
  }, [images.newImgCount, images.newImagesLoaded, actions.refresh]);

  const collageCallback = (someTree) => treeState.addTreeToDB(someTree);

  const handleRemoveImage = (imgTitle) => {
    const newImagesArr = images.remove(imgTitle);
    treeState.reAdjust(
      newImagesArr,
      constants.tolerance,
      dimensions.imageWidth,
      dimensions.imageHeight,
      options.options
    );
  };

  const newCollage = () => {
    const ar = treeState.tree.root.value.aspectRatio;
    console.log('in generate thumbnail', ar, 500 * ar, 500);
    const collageID = Date.now();
    const thumbnailCanvas = document.createElement('canvas');
    const thumbnailTree = treeState.tree.copy();
    thumbnailTree.addSize(500 * ar, 500, ar);
    thumbnailCanvas.width = 500 * ar;
    thumbnailCanvas.height = 500;
    thumbnailTree.CanvasDisplay(
      500 * ar,
      500,
      thumbnailCanvas,
      (500 * ar) / dimensions.width
    );
    setTimeout(() => {
      thumbnailCanvas.toBlob((blob) => {
        setThumbnail(blob);
        const link = document.createElement('a');
        link.download = `${collageID}-thumbnail.png`;
        link.href = URL.createObjectURL(blob);
        link.click();
        URL.revokeObjectURL(link.href);
        link.href = null;
      });
    }, '2000');
    const thumbnailDetails = {
      title: `${collageID}-thumbnail.jpg`,
      src: '',
      naturalHeight: 500,
      naturalWidth: 500 * ar,
      aspectRatio: ar,
      type: 'image/png',
    };
    const collageDetails = {
      collage_id: collageID.toString(),
      name: 'TestCollage',
      created_at: '2021-02-18T21:54:42.123Z',
      last_updated_at: '2021-02-18T21:54:45.123Z',
      images: images.removeImageData(images.originalImages),
      tree: treeState.tree.removeImageData(),
      thumbnail: thumbnailDetails,
    };
    console.log(collageDetails);
    return collageapi.newCollage(collageID, collageDetails);
  };

  useEffect(() => {
    if (
      options.options.text.length > 0 &&
      Object.keys(treeState.tree).length > 0
    ) {
      const tH = helpers.wrapText(
        options.options.text,
        0,
        0,
        dimensions.width,
        20,
        25
      );
      dimensions.setTextHeight(tH.length * 25);
      const [w, h] = helpers.getCollageDimension(
        dimensions.width,
        dimensions.height - tH.length * 25,
        treeState.tree.root.value.aspectRatio
      );
      dimensions.setImageWidth(w);
      dimensions.setImageHeight(h);
    } else {
      dimensions.setTextHeight(0);
      options.handlePositionChange(true);
      dimensions.setImageWidth(dimensions.width);
      dimensions.setImageHeight(dimensions.height);
    }
  }, [
    dimensions.width,
    dimensions.height,
    treeState.tree,
    options.options.text,
  ]);

  return [
    thumbnail,
    constants,
    dimensions,
    options,
    actions,
    images,
    treeState,
    handleClear,
    collageCallback,
    handleRemoveImage,
    newCollage,
  ];
}
